.experience__details,
.experience__input {
  display: none;
}

.experience__input:checked +
.experience__title +
.experience__details{
  display: inherit;
}

.experience__header {
  color: $color-periwinkle-gray;
  @extend %header;
}

.experience__title {
  background-color: $color-gumbo;
  color: $color-white;
  display: block;
  font-size: 1.7rem;
  padding: 2rem;
  cursor: pointer;
}

.experience__title:hover {
  .my-cv__icon--plus {
    color: green;
  }
}

.experience__details {
  border: 0.2rem solid $color-gumbo;
}

.experience__top {
  display: flex;
  justify-content: space-between;
}

.experience__date {
  font-style: italic;
}

.experience__company {
  font-weight: bold;
}

.experience__company,
.experience__date {
  font-size: 1.2rem;
  padding: 1rem;
  user-select: none;
}

.experience__responsability {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  line-height: 1.1;
  user-select: none;

  &:nth-child(odd) {
    background-color: $color-fantasy;
  }

  &:nth-child(even) {
    background-color: $color-twilight;
  }
}

.my-cv__icon--plus {
  float: right;
  pointer-events: none;
}

.experience__input:checked +
.experience__title {
  .my-cv__icon--plus {
    transform: rotateZ(45deg);
    pointer-events: all;
    cursor: pointer;
    &:hover{
      color: red;
    }
  }
}