.landscape,
.landscape__input {
  display: none;
}

.landscape {
  position: fixed;
  top: 0%;
  width: 100vw;
  height: 100vh;
  background-color: $color-manatee;
  transition: .5s ease-in-out;
}
.landscape__button {
  display: block;
  background-color: $color-fiord;
  color: $color-white;
  position: fixed;
  top: 1rem;
  left: 1rem;
  font-size: 2.2rem;
  padding: 1rem;
  border-radius: 0.5rem;
  text-decoration: none;
  border: o.15rem solid $color-white;
  cursor: pointer;
  // visibility: hidden;
}

.landscape__description {
  padding: 2rem 0;
}

@media (orientation: landscape) {
  .landscape {
    display: flex;
    align-items: center;
    justify-content: center;
    color: $color-white;
    font-size: 2rem;
  }
  .landscape__list {
    list-style-type: disc;
  }
  .landscape__message--show {
    display: none;
  }
  .landscape__message--hide {
    display: block;
  }
  #landscape:checked + .landscape {
    top: -100%;
    .landscape__message--show {
      display: block;
    }
    .landscape__message--hide {
      display: none;
    }
  }
}